.movieCard {
    display: flex;
}

.image {
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.movieInfo {
    margin-left: 35px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.movieItem {
    font-weight: 700;
    color: #3f51b5;
    font-size: 15px;
    text-transform: uppercase;
}

.voteSum {
    margin-left: 5px;
    font-weight: 400;
    color: #2a363b;
    font-size: 15px;
    text-transform: none;
}

.movieTitle {
    margin-top: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.16;
    letter-spacing: 0.03em;
    text-align: center;
}

.description {
    text-align: left;
}

.wrapper {
    text-align: left;
    display: flex;
}

.image {
    margin-right: 30px;
    margin-bottom: 0;

    max-width: 320px;
    height: 400px;
    border-radius: 2px;
    object-fit: cover;
}